import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, tap } from 'rxjs/operators';

import { Util } from '../other/util';
import { LogService } from './log.service';
import { SpsEvent } from '../models/spsEvent';

const httpOptions = {
  headers: new HttpHeaders({ 'Content-Type': 'application/json' })
};

@Injectable({
  providedIn: 'root'
})
export class ExternalScheduleService {

  private serviceUrl: string = '';

  constructor(private http: HttpClient,
    private logService: LogService) {
  }

  init(serviceUrl: string) {
    this.serviceUrl = serviceUrl;
  }

  /**************************************************************************
  * POST /runInternalScheduler/${planId}
  **************************************************************************/
  runInternalScheduler(planId: string): Observable<string> {
    this.logService.debug('ExternalScheduleService.runInternalScheduler');
    const url = Util.urlJoin(this.serviceUrl, '/runInternalScheduler/' + planId);

    return this.http.post<any>(url, httpOptions).pipe(
      tap(res => this.logService.debug(`    RunInternal Scheduler successfull`)),
      map(res => res)
    );
  }

  /**************************************************************************
  * POST /publishToLaso/${planId}
  **************************************************************************/
  publishToLaso(planId: string): Observable<string> {
    this.logService.debug('ExternalScheduleService.publishToLaso');
    const url = Util.urlJoin(this.serviceUrl, '/publishToLaso/' + planId);

    return this.http.post<any>(url, httpOptions).pipe(
      tap(res => this.logService.debug(`    Published To Laso successfully`)),
      map(res => res.status)
    );
  }

  /**************************************************************************
  * POST /spsEvent/${planId}
  **************************************************************************/
  create(spsEvent: SpsEvent): Observable<string> {
    this.logService.debug('ExternalScheduleService.spsEvent');
    const url = Util.urlJoin(this.serviceUrl, '/spsEvent');

    return this.http.post<any>(url, spsEvent, httpOptions).pipe(
      tap(res => this.logService.debug(`    Stored sps Event successfully`)),
      map(res => res)
    );
  }

  /**************************************************************************
  * POST /publishToLaso/${planId}
  **************************************************************************/
  getLasoProhibit(prohibitId: string): Observable<SpsEvent> {
    this.logService.debug('ExternalScheduleService.getLasoProhibit');
    const url = Util.urlJoin(this.serviceUrl, '/getLasoProhibit/' + prohibitId);

    return this.http.get<any>(url, httpOptions).pipe(
      tap(res => this.logService.debug(`    Retrieved Laso Prohibit successfully`)),
      map(res => res)
    );
  }

  /**************************************************************************
  * POST /assetsWithMx/${planId}
  **************************************************************************/
  getAssetsWithMx(planId: string): Observable<string[]> {
    this.logService.debug('ExternalScheduleService.assetsWithMx');
    const url = Util.urlJoin(this.serviceUrl, '/assetsWithMx/' + planId);

    return this.http.get<any>(url, httpOptions).pipe(
      tap(res => this.logService.debug(`    Retrieved assets with Mx successfully`)),
      map(res => res.strings)
    );
  }

}
